@import '~@/styles/variables/colors';
@import '~@/styles/variables/font';

.logo {
  display: flex;
  align-items: center;
  width: 200px;
}

.logo__text {
  text-transform: uppercase;
  color: $green-deep;
  font-size: $font-size-base;
  font-weight: 900;
}

.logo--secondary .logo__text {
  opacity: .6;
}

.logo__image {
  width: 53px;
  height: 58px;
}

.logo__text + .logo__image {
  margin-left: 30px;
}
