@import '~@/styles/variables/';

.stage__controls {
  display: grid;
  grid-template: 1fr / repeat(3, 1fr);
  grid-column-gap: $indent-base;
  justify-content: end;
  align-items: end;
}

.stage__controls-button {
  grid-column: 1 / 2;
}
.stage__controls-select {
  grid-column: 2 / 4;
}

.stage__info-item {
  h3,
  p {
    margin: 0 0 $indent-half 0;
    color: $green;
  }
}

.stage__info-item--disabled {
  h3,
  p {
    margin: 0 0 $indent-half 0;
    color: $grey;
  }
}

.stage__text {
  margin-top: $indent-double;

  p {
    margin-top: 0;
    color: $grey;
    line-height: 1.6;

    &:not(:last-child) {
      margin-bottom: $indent-half;
    }
  }
}

.stage__mark {
  margin-top: $indent-double;
}