@import '~@/styles/variables/colors';
@import '~@/styles/variables/font';
@import '~@/styles/common/grid';
// if necessary, it is possible to transfer the variable to global styles, but at the moment it is needed only in this file
$large-plus-window-width: 1304px;

.header {
  width: 100%;
  padding-top: 16px;
  padding-bottom: 16px;
  background: $light;
  font-size: $font-size-base;
  line-height: 19px;

  @media screen and (min-width: $large-plus-window-width) {
    position: relative; // for a .header__logout button, that changes position on wide screens
  }
}

.header__nav {
  display: flex;
  align-items: center;
}

.header__nav-list {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin: 0;
  padding: 0;
  list-style: none;
}

.header__logo {
  & + .header__nav-list {
    margin-left: 38px;
  }
}

.header__nav-item {
  padding-right: 10px;
  padding-left: 10px;

  &:hover {
    .header__link {
      color: $link-hover-color;
    }
  }

  &.is-active {
    border-bottom: 2px $green solid;
    .header__link {
      color: $green;
    }
  }
}

.header__link {
  transition: color .35s;
  white-space: nowrap;
  text-decoration: none;
  line-height: $indent-double;
}

.header__link--active {
  text-decoration: underline;
}

.header__account {
  margin-left: 14px;
}

.header__user {
  max-width: 220px;
  margin-left: 14px;

  @media screen and (min-width: $large-plus-window-width) {
    max-width: 248px;
  }
}

.header__logout {
  margin-left: auto;

  @media screen and (min-width: $large-plus-window-width) {
    position: absolute;
    top: 0;
    right: 0;
    bottom: 0;
    display: flex;
    justify-content: center;
    align-items: center;
    width: 76px;
    margin: auto;
  }
}

.header__logout-button {
  @media screen and (min-width: $large-plus-window-width) {
    width: 100%;
    height: 100%;
    color: white;
    background-color: $grey-light;

    &:hover {
      color: $grey-light;
      background-color: transparent;
    }
  }
}

.logout-icon {
  @media screen and (min-width: $large-plus-window-width) {
    width: 27px;
    height: 25px;
  }
}
