@import '~@/styles/variables/layout';
@import '~@/styles/variables/indents';

.container {
  width: $default-content-width;
  margin-right: auto;
  margin-left: auto;
  padding-right: $indent-half;
  padding-left: $indent-half;
}

.container--margin {
  margin-bottom: $indent-base * 4;
}

.grid {
  display: grid;
  grid-template: auto / [full-start] repeat(5, 1fr) [full-end];
  grid-gap: $indent-base * 1.5 $indent-base;
  align-items: start;
}

.grid--centered {
  align-items: center;
}

// допускается использование модификатора сетки без указания класса-родителя
.grid__item--single {
  grid-column: span 1;
}
.grid__item--double {
  grid-column: span 2;
}
.grid__item--triple {
  grid-column: span 3;
}
.grid__item--full {
  grid-column: full;
}

.form-grid {
   > div {
    @extend .grid;
  }
}