@import '~@/styles/variables';

.auth-input {
  position: relative; // for .auth-input__label
  padding-top: 24px;

  &:focus-within,
  &.is-full {
    .auth-input__label {
      bottom: calc(100% - 24px);
      color: $grey-light;
      font-size: 11px;
      line-height: 13px;
    }
  }
}

.auth-input__label {
  position: absolute;
  bottom: 11px;
  left: 0;
  padding: 0;
  transition: all .2s;
  color: $grey;
  font-weight: 400;
}

.auth-input__control {
  height: initial;
  padding: 10px 0;
  color: inherit;
  border: none;
  background: transparent;
}

.auth-input__control-wrapper {
  position: relative; // for .auth-input__icon
  color: $green;
  border-bottom: 1px solid $green;

  &:after {
    position: absolute;
    z-index: 10;
    top: 0;
    right: 0;
    bottom: 0;
    display: block;
    width: $indent-double;
    content: '';
    background-image: linear-gradient(to left, #F1F1F1 80%, transparent);
  }
}

.auth-input__icon {
  position: absolute;
  z-index: 20;
  top: 10px;
  right: 4px;
  width: 16px;
  height: 16px;
  color: inherit;

  svg {
    display: block;
  }
}

.auth-input__icon--password {
  padding: 0;
  border: none;
  background: none;

  &:hover {
    cursor: pointer;
  }
}

.auth-input-wrapper {
  &.error {
    .auth-input__control-wrapper {
      color: $red;
      border-bottom-color: $red;
    }
  }

  .errors {
    margin-top: 3px;
    color: $rose;
    font-size: 11px;
    line-height: 13px;
  }
}
