@import '~@/styles/variables/';

.list {
  margin: 0;
  padding: 0;
  list-style: none;
}

.list--horizontal {
  display: grid;
  grid-template: auto / repeat(5, 1fr);
  grid-gap: $indent-base;
  flex-flow: row wrap;
}

.list--vertical {
  flex-flow: column nowrap;

   > * {
    flex-basis: 100%;
    margin-bottom: $indent-half;
  }
}