@import '~@/styles/variables/';

.report {
  .container {
    width: 716px;
    padding: 0 $indent-base * 1.5;
  }

  .container--margin {
    margin-bottom: $indent-base;
  }

  .grid {
    grid-column-gap: $indent-half;
  }

  p {
    line-height: 1.5;
  }
}

.report__actions {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.report__controls {
  display: grid;
  grid-template: 1fr / repeat(3, 1fr);
  grid-column-gap: $indent-half;
  justify-content: end;
  align-items: end;
}

.report__controls-button {
  grid-column: 3 / 4;
}
.report__controls-select {
  grid-column: 1 / 3;
}

.report__text {
  margin-top: $indent-base;
  p {
    margin-top: 0;
    color: $grey;
    line-height: 1.6;

    &:not(:last-child) {
      margin-bottom: $indent-half;
    }
  }
}

.report__label {
  display: flex;
  align-items: center;
  height: 55px;
  margin-bottom: $indent-base * 2;
  padding: 0 $indent-base * 2;
  background-color: $green;

  p {
    color: white;
    font-size: 20px;
    font-weight: 500;
  }
}

.report__content {
  h4 {
    padding: 0 0 $indent-base $indent-quart;
    color: $grey;
    border-bottom: 1px $grey-light solid;
    font-weight: 500;
    margin-top: 0;
  }
}

.report__info {
  margin-bottom: $indent-base * 1.5;

  p {
    margin: $indent-quart 0;
    color: $green;
    font-weight: 500;
  }
}
