@import '~@/styles/common/grid';
@import '~@/styles/common/mixins';
@import '~@/styles/variables';

.auth {
  width: 100%;
  height: 100vh;
  min-height: 768px;
  background: url('~@/assets/images/auth-bg.jpg') center no-repeat;
  background-size: cover;
  @include retina {
    background: url('~@/assets/images/auth-bg2x.jpg') center no-repeat;
    background-size: cover;
  }
}


.auth__container {
  display: flex;
  justify-content: flex-end;
  align-items: stretch;
  height: 100%;
}

.auth__content {
  width: 100%;
  max-width: 471px;
}

.auth-main__auth-form {
  width: 100%;
  max-width: 225px;
}

.auth-main__description {
  min-height: 94px;
  margin-top: 16px;
  margin-bottom: 16px;
}

.auth-main {
  padding: $indent-base * 3 40px 75px 60px;
  background: $light;
}

.auth-main__logo {
  margin-bottom: 71px;
}

.auth-main__links {
  margin-top: 10px;
}

.auth-links {
  padding: 0;
  list-style: none;
}

.auth-links__item + .auth-links__item {
  margin-top: 10px;
}

.auth-links__link {
  cursor: pointer;
  text-decoration: underline;
  color: $grey-light;
}

// исключительный случай: обработка полей авторизации.
// Внимание! Именование стиля задаются в .js файле схемы компонента
.field-AuthInput.error {
  position: relative;

  input {
    border-color: $red;
  }

  .errors {
    position: relative;
    z-index: 10;
    top: auto;
    right: auto;
    display: flex;
    justify-content: flex-start;
    align-items: center;
    width: auto;
    height: auto;
    cursor: pointer;
    color: $red;
    background-color: transparent;
    font-size: 11px;
    font-weight: 500;
    line-height: 13px;
    padding: 0;
    opacity: .5;

    span {
      display: block;
      justify-content: flex-start;
    }

    &:before {
      content: none;
    }

    &:hover {
      display: flex;
      width: auto;
      max-width: unset;
      height: auto;
    }
  }
}
