@import '~@/styles/';

.app-button {
  display: flex;
  justify-content: center;
  align-items: center;
  max-width: 100%;
  padding: 0 8px;
  cursor: pointer;
  transition: background-color .35s, color .35s;
  text-align: center;
  color: white;
  border: none;
  outline: 0;
  background-color: $green;
  line-height: 44px;

  &:hover,
  &.is-active {
    background-color: $green-deep;
  }
  &:disabled,
  &.is-disabled {
    background-color: $grey-light;
    cursor: auto;
  }
}

.app-button--expanded {
  width: 100%;
}

.app-button--close {
  position: relative;
  width: 24px;
  min-width: unset;
  height: 24px;
  background-color: $grey-light;

  &:before,
  &:after {
    position: absolute;
    top: 50%;
    left: 50%;
    display: inline-block;
    width: 13px;
    height: 2px;
    content: '';
    transition: all .35s ease-in;
    background-color: white;
    background-image: none;
  }

  &:before {
    transform: translateX(-50%) translateY(-50%) rotate(45deg);
  }
  &:after {
    transform: translateX(-50%) translateY(-50%) rotate(-45deg);
  }

  &:hover {
    opacity: 1;
    &:before {
      transform: translateX(-50%) translateY(-50%) rotate(-45deg);
    }
    &:after {
      transform: translateX(-50%) translateY(-50%) rotate(45deg);
    }
  }
}

.app-button--spare {
  min-width: unset;
  color: $grey;
  background-color: transparent;

  &:hover {
    color: $green-deep;
    background-color: transparent;
  }

   > * {
    margin: 0 4px;
  }
}

.app-button__icon {
  // this class is necessary to save the size of icon in case with a lot of text
  flex-shrink: 0;
}

.app-button__text {
  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis;
}
