@import '~@/styles/variables/';

.v-select {
  width: 100%;

  .vs__dropdown-toggle {
    height: 44px;
    padding-bottom: 0;
    border: 2px $green solid;
    border-radius: 0;
  }

  .vs__selected {
    position: absolute;
    overflow: hidden;
    height: 40px;
    margin: 0;
    white-space: nowrap;
    color: $grey;
  }

  .vs__dropdown-menu {
    padding: 0;
    border-width: 0 2px 2px;
    border-style: solid;
    border-color: $green;
    border-radius: 0;

    li {
      position: relative;
      overflow-x: hidden;
      color: $grey;

      &:hover {
        color: $grey;
        background-color: $light;

        &:after {
          background-image: linear-gradient(to left, $light 30%, transparent);
        }
      }

      &:after {
        position: absolute;
        z-index: 10;
        top: 0;
        right: 0;
        bottom: 0;
        display: block;
        width: $indent-base;
        content: '';
        background-image: linear-gradient(to left, white 30%, transparent);
      }
    }
  }

  li.vs__dropdown-option--highlight {
    color: $grey;
    background: $light;

    &:after {
      position: absolute;
      z-index: 10;
      top: 0;
      right: 0;
      bottom: 0;
      display: block;
      width: $indent-base;
      content: '';
      background-image: linear-gradient(to left, $light 30%, transparent);
    }
  }

  &.vs--single.vs--open {
    .vs__selected {
      top: 4px;
      bottom: 0;
      opacity: 1;
      border-color: transparent;
    }

    .vs__selected-options {
      &:after {
        top: 5px;
      }
    }
  }

  .vs__search {
    position: absolute;
    height: 40px;
    margin: 0;
  }

  .vs__selected-options {
    position: relative;
    overflow: hidden;

    &:after {
      position: absolute;
      z-index: 10;
      top: 2px;
      right: 0;
      bottom: 0;
      display: block;
      width: $indent-base;
      content: '';
      background-image: linear-gradient(to left, white 30%, transparent);
    }
  }
}
