h1,
h2,
h3,
h4,
h5,
h6 {
  color: $black;
}

// heading font size
@for $i from 1 through length($headings-size-list) {
  $heading: h#{$i};

  #{$heading} {
    font-size: map-get($headings-size-list, $heading);
  }
}

p {
  color: $text-color;
  font: $default-font;
  line-height: 1.3;
}

ul,
ol {
  color: $grey;
  font-size: $font-size-base;
}

a {
  text-decoration: underline;
  color: $link-color;

  &:hover {
    color: $link-hover-color;
  }
}

.color {
  color: inherit;
}

.color--black {
  color: $black !important;
}
