@import '~@/styles/variables/';

.report-list {
  margin: 0;
  padding: 0;
  list-style: none;
  margin-bottom: $indent-base * 3;
}

.report-list__item {
  position: relative;
  display: flex;
  justify-content: space-between;
  align-items: flex-end;
  width: 100%;
  margin-bottom: $indent-base * 1.25;

  &:before {
    position: absolute;
    z-index: 0;
    bottom: 6px;
    display: inline-block;
    width: 100%;
    content: '';
    border-bottom: 1px $light solid;
  }

  &.is-disabled {
    p,
    span {
      color: $grey-light;
    }
  }

  p {
    position: relative;
    z-index: 10;
    margin: 0;
    padding: 0;
    color: $green;
    font-size: 20px;
    font-weight: 500;
  }

  span {
    background-color: white;
    font-size: 20px;
    font-weight: 500;
  }
}

.report-list--main {
  margin-top: $indent-base * 1.5;

  .report-list__item {
    p:first-child {
      color: $black;
    }
  }
}
