@import '~@/styles/variables/';

.heading__title {
  h1 {
    margin: 0;
    font-weight: 500;
    line-height: 1;
  }
  h2,
  h3,
  h4,
  h5,
  h6 {
    margin: 0;
    font-weight: 500;
    line-height: 1.3;
  }
}

.heading__subtitle {
  margin: $indent-half 0;

  h4 {
    margin: 0;
    color: $grey;
  }
}

.heading__info {
  margin-top: $indent-half;

  ul {
    margin: 0;
    padding: 0;
    list-style: none;
  }

  li {
    color: $green;
    font-weight: 500;

    &:not(:last-child) {
      margin-bottom: $indent-quart;
    }
  }
}

.heading--section {
  .heading__title {
    padding-left: $indent-half;
    border-left: 4px $green solid;
  }
}