@import '~@/styles/variables/';

html {
  box-sizing: border-box;
  font-size: 10px;
}

*,
*:before,
*:after {
  box-sizing: inherit;
}

body {
  background-color: white;
  font: $default-font;
  line-height: inherit;
}

img {
  display: block;
  max-width: 100%;
  height: auto;
}

table {
  width: 100%;
  border-collapse: collapse;

  th {
    padding: 0 10px;
    color: $black;
    border-left: 4px $green solid;
    font-size: $font-size-large;
    font-weight: bold;
    line-height: 29px;
  }

  th,
  td {
    text-align: left;
  }

  td {
    padding-top: 8px;
  }

  h3,
  p {
    margin: 8px 0;
    color: $text-color;
  }

  h3 {
    font-weight: 500;
  }
}

.wrapper {
  position: relative;
}
