// Отлючение стрелочек для number инпутов
input[type='number']::-webkit-outer-spin-button,
input[type='number']::-webkit-inner-spin-button {
  margin: 0;
  -webkit-appearance: none;
}
input[type='number'] {
  -moz-appearance: textfield;
}

// global input styles
input {
  width: 100%;
  height: 44px;
  padding: 0 $indent-half;
  color: $green;
  border: 2px solid $green;
  outline: 0;

  &::placeholder {
    color: $grey;
  }
}

label {
  display: flex;
  align-items: flex-end;
  height: 44px;
  padding: $indent-quart $indent-half;
  color: $grey;
  font-weight: 500;
  line-height: 1.1;
}

.label--top {
  label {
    align-items: flex-start;
  }
}

.label--middle {
  label {
    align-items: center;
  }
}

// данный класс генерирруется плагином vue-form-generator и используется для кастомизации форм на проекте
.field-Input {
  .wrapper {
    &:after {
      position: absolute;
      z-index: 10;
      top: 3px;
      right: 3px;
      bottom: 3px;
      display: block;
      width: $indent-base;
      content: '';
      background-image: linear-gradient(to left, white 30%, transparent);
    }
  }
}

fieldset {
  padding: 0;
  border: 0;
}

textarea {
  width: 100%;
  padding: 8px;
  resize: none;
  color: $green;
  border: 2px $green solid;
  outline: 0;
  line-height: 1.3;

  &::placeholder {
    color: $grey;
  }
}

// styles, used for form generation
.combined {
   > div {
    display: grid;
    grid-template: auto / repeat(2, 1fr);
    grid-column-gap: $indent-half;
  }

  .field-label {
    grid-column: 1 / 3;
  }

  label {
    height: auto;
  }
}

.error {
  position: relative;

  input {
    border-color: $red;
  }

  .errors {
    position: absolute;
    z-index: 10;
    top: 0;
    right: 0;
    display: flex;
    justify-content: center;
    align-items: center;
    width: 11px;
    height: 11px;
    padding: $indent-half;
    cursor: pointer;
    color: white;
    background-color: $red;
    font-size: 11px;
    font-weight: 500;
    line-height: 13px;

    span {
      display: none;
    }

    &:before {
      content: '!';
      font-size: 9px;
      line-height: 0;
    }

    &:hover {
      display: flex;
      width: auto;
      max-width: 75%;
      height: 100%;

      &:before {
        content: none;
      }

      span {
        display: inline-block;
      }
    }
  }
}

// для демонстрации заказчику и выбора варианта показа ошибки
.error--additional {
  .errors {
    position: inherit;
    &:hover {
      &:before {
        content: '!';
      }
    }

    span {
      position: absolute;
      top: auto;
      bottom: -5px;
      left: 0;
      display: block;
      min-height: 40px;
    }
  }
}

// styles, used for form generation end
